.formContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1em;
  margin-top: 1em;
}

.formBody {
  background-color: rgb(34, 34, 34);
  color: whitesmoke;
  padding: 2em;
  width: 50%;
}
.formBody label {
  font-size: 1.3em;
}
.formBody input {
  width: 100%;
  height: 8vh;
  padding: 12px 20px;
  margin: 8px 0;
  font-size: 1.5em;
  box-sizing: border-box;
  color: black;
}
.formBody input:focus {
  border: 3px solid #555;
}
.formBody input:-moz-placeholder-shown {
  color: lightgray;
  font-size: 1em;
}
.formBody input:-ms-input-placeholder {
  color: lightgray;
  font-size: 1em;
}
.formBody input:placeholder-shown {
  color: lightgray;
  font-size: 1em;
}
.formBody button[type=submit] {
  width: 100%;
  height: 20%;
  padding: 12px 20px;
  margin-top: 1em;
  font-size: 1.5em;
}

@media screen and (max-width: 1500px) {
  .formBody {
    width: 70%;
  }
}
@media screen and (max-width: 800px) {
  .formBody {
    width: 100%;
  }
}
@media screen and (max-width: 400px) {
  .formBody label {
    font-size: 1em;
  }
  .formBody input[type=text] {
    height: 4vh;
    font-size: 1em;
  }
}
.checkContainer {
  display: flex;
  justify-items: center;
  align-items: center;
  gap: 0.5em;
}
.checkContainer label {
  display: inline-block;
}

.formGroups {
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  grid-template-columns: 1fr;
  gap: 0.5em;
}

.actualDegree {
  display: flex;
  flex-wrap: nowrap;
  gap: 0.5em;
  align-items: center;
  justify-items: center;
}

.hidden {
  display: none;
}

.modalBox {
  overflow: scroll;
}

.closeButton {
  display: flex;
  align-items: flex-start;
  justify-content: flex-end;
}/*# sourceMappingURL=EducationForm.module.css.map */